import Vue from 'vue'
import App from './App'
import router from './router'
import 'h_ui/dist/h_ui.min.css';
import { Button, Select, Icon, Spin, Message, Input, Typefield } from 'h_ui'



Vue.config.productionTip = false
Vue.component('hButton', Button)
Vue.component('hSelect', Select)
Vue.component('hIcon', Icon)
Vue.component('hSpin', Spin)
Vue.component('hMessage', Message)
Vue.component('hInput', Input)
Vue.component('hTypefield', Typefield)
Vue.prototype.$hMessage = Message;




/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



