<template>
  <div
    :class="classes"
    v-clickoutside="{ trigger: 'mousedown', handler: handleClose }"
    :style="multiplestyle"
    ref="select"
  >
    <div
      ref="reference"
      :title="titleTip"
      :class="selectionCls"
      :style="selectInputStyles"
      :tabindex="selectTabindex"
      @click="toggleMenu"
      @keyup="keyup"
      @keydown="keydown"
    >
      <!-- 多选时输入框内选中值模拟 -->
      <template v-if="multiple && !collapseTags">
        <div
          class="h-tag"
          v-for="(item, index) in selectedMultiple"
          :key="index"
        >
          <span class="h-tag-text" v-if="showValue">{{ item.value }}</span>
          <span class="h-tag-text" v-if="!showValue">{{ item.label }}</span>
          <Icon name="close" @click.native.stop="removeTag(index, item)"></Icon>
        </div>
      </template>
      <!-- 多选时输入框压缩展示个数 -->
      <template v-if="multiple && collapseTags && selectedMultiple.length > 0">
        <div class="h-tag">
          <span class="h-tag-text">{{ selectedMultiple[0].label }}</span>
          <Icon
            name="close"
            @click.native.stop="removeTag(0, selectedMultiple[0])"
          ></Icon>
        </div>
        <div
          class="h-tag"
          v-if="
            multiple &&
              collapseTags &&
              selectedMultiple &&
              selectedMultiple.length > 1
          "
        >
          <span class="h-tag-text">+{{ selectedMultiple.length - 1 }}</span>
        </div>
      </template>
      <!-- 下拉输入框模拟（非远程搜索时渲染）  -->
      <span
        :class="[prefixCls + '-placeholder']"
        v-show="showPlaceholder && (!filterable || showBottom)"
        >{{ localePlaceholder }}</span
      >
      <span
        :class="[
          prefixCls + '-selected-value',
          showCloseIcon && !disabled && !readonly
            ? prefixCls + '-selected-value-close'
            : ''
        ]"
        v-show="!showPlaceholder && !multiple && !(filterable && !showBottom)"
        >{{ selectedSingle }}</span
      >
      <!-- 下拉输入框(远程搜索时渲染) -->
      <input
        ref="input"
        type="text"
        v-model="query"
        autocomplete="off"
        :class="[
          prefixCls + '-input',
          showCloseIcon ? prefixCls + '-selected-value-close' : ''
        ]"
        :style="inputVisibleStyle"
        :disabled="disabled"
        :readonly="readonly || !editable"
        :placeholder="showPlaceholder ? localePlaceholder : ''"
        :tabindex="tabindex"
        v-if="filterable && !showBottom"
        @focus="handleFocus"
        @blur="handleBlur"
        @keyup="handleInputKeyup($event)"
        @keydown="handleInputKeydown($event)"
        @keydown.delete="handleInputDelete"
        :maxlength="maxlength"
      />
      <!-- 单选时清空按钮 -->
      <Icon
        name="close"
        :class="[prefixCls + '-arrow']"
        v-if="showCloseIcon"
        @click.native.stop="handleIconClose"
        ref="close"
      ></Icon>
      <Icon
        name="unfold"
        :class="[prefixCls + '-arrow']"
        v-if="!remote && isArrow"
        ref="arrowb"
      ></Icon>
      <Icon
        :name="remoteIcon"
        :class="[prefixCls + '-arrow']"
        v-if="showRemoteIcon"
        ref="searchb"
      ></Icon>
    </div>
    <drop
      ref="dropdown"
      v-transfer-dom
      :show="dropVisible"
      :dropWidth="dropWidth"
      :maxDropWidth="maxDropWidth"
      :placement="placement"
      :autoPlacement="autoPlacement"
      :data-transfer="transfer"
      :widthAdaption="widthAdaption"
      :class="dropdownCls"
    >
      <div
        :class="content"
        :style="notFound ? 'padding: 0px;' : ''"
        @scroll="handleSelectScroll"
        ref="content"
        @click="handleclick"
      >
        <span v-if="filterable && showBottom" :class="checkHeadClass">
          <Checkbox
            v-model="selectHead"
            v-if="checkToHead && multiple"
          ></Checkbox>
          <input
            type="text"
            v-model="query"
            :disabled="disabled"
            :readonly="!editable || readonly"
            :class="[prefixCls + '-content-input']"
            :placeholder="localeSearchHolder"
            autocomplete="off"
            @focus="handleFocus"
            @blur="handleBlur"
            @keydown.delete="handleInputDelete"
            @keydown="keydown"
            :tabindex="tabindex"
            :maxlength="maxlength"
            ref="input"
          />
        </span>
        <span
          v-if="hideMult && multiple"
          :class="hideMultHead"
          @click="toggleSelect(!isSelectAll)"
          >{{ t("i.select.checkAll") }}</span
        >
        <div v-if="showHeader">
          <slot name="header">
            <template v-if="buttonToTop && multiple">
              <span :class="[prefixCls + '-btnToTop']">
                <Checkbox
                  v-model="selectHead"
                  @click.native.stop="toggleSelect(!isSelectAll)"
                  >{{ t("i.select.checkAll") }}</Checkbox
                >
                <Button
                  size="small"
                  :class="[prefixCls + '-btnToTop-invert']"
                  @click="toggleSelect(false, true)"
                  >{{ t("i.select.checkNone") }}</Button
                >
              </span>
            </template>
          </slot>
        </div>
        <ul
          v-show="notFoundShow && !enableCreate"
          :class="[prefixCls + '-not-found']"
        >
          <li>{{ localeNotFoundText }}</li>
        </ul>
        <ul
          v-show="
            (!notFound && !remote) ||
              (remote && !loading && !notFound) ||
              enableCreate
          "
          :class="[prefixCls + '-dropdown-list']"
        >
          <!-- 多选、支持搜索和新建条目的情况下，如果检索词不匹配任何一个条目时，显示此项供用户选择 -->
          <h-option
            ref="createdOption"
            v-if="enableCreate && showNewOption"
            :value="query"
            created
            >{{ query }}</h-option
          >
          <slot></slot>
        </ul>
        <ul v-show="loading" :class="[prefixCls + '-loading']">
          <Icon
            v-show="showLoadingIcon"
            class="h-load-loop"
            name="load-c"
            style="font-size: 14px; margin-right: 5px;"
          ></Icon>
          {{
            localeLoadingText
          }}
        </ul>
        <ul v-show="isComputed" :class="[prefixCls + '-not-data']">
          {{
            localeNoMoreText
          }}
        </ul>
      </div>
      <div v-if="showFooter" :class="checkAll">
        <slot name="footer">
          <template
            v-if="
              (isCheckall && multiple && !notFoundShow && !buttonToTop) ||
                (enableCreate && showNewOption)
            "
          >
            <Button size="small" @click="toggleSelect(false, true)">{{
              t("i.select.checkNone")
            }}</Button>
            <Button type="primary" size="small" @click="toggleSelect(true)">{{
              t("i.select.checkAll")
            }}</Button>
          </template>
        </slot>
      </div>
    </drop>
  </div>
</template>
<script>
import Icon from '../Icon/Icon.vue'
import Drop from '../../common/Dropdown.vue'
import clickoutside from '../../directives/clickoutside'
import TransferDom from '../../directives/transfer-dom'
import Checkbox from '../Checkbox/Checkbox.vue'
import Message from '../Message/index.js'
import {
  oneOf,
  findComponentChildren,
  findComponentParent,
  getScrollBarSize,
  getBarBottom,
  typeOf,
  findInx
} from '../../util/tools'
import Emitter from '../../mixins/emitter'
import Locale from '../../mixins/locale'
import _ from '../..//util'
const prefixCls = 'h-select'
export default {
  name: 'Select',
  mixins: [Emitter, Locale],
  components: { Icon, Drop, Checkbox },
  directives: { clickoutside, TransferDom },
  props: {
    tabToSave: {
      // tab键保存新建条目
      type: Boolean,
      default: false
    },
    filterMethod: {
      type: Function
    },
    chooseToRemoteMethod: {
      type: Boolean,
      default: false
    },
    multipleNumber: {
      type: [Number , String],
      default: null
    },
    // 当组件在异步查询，模糊搜索时，是否显示 loading的动画图标
    showLoadingIcon: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    editable: {
      type: Boolean,
      default: true
    },
    value: {
      type: [String, Number, Array],
      default: ''
    },
    // 使用时，也得设置 value 才行
    label: {
      type: [String, Number, Array],
      default: ''
    },
    multiple: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    multClearable: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String
    },
    filterable: {
      type: Boolean,
      default: false
    },
    accuFilter: {
      type: Boolean,
      default: false
    },
    remote: {
      type: Boolean,
      default: false
    },
    remoteMethod: {
      type: Function
    },
    remoteIcon: {
      type: String,
      default: ''
    },
    loading: {
      type: Boolean,
      default: false
    },
    loadingText: {
      type: String
    },
    size: {
      validator(value) {
        return oneOf(value, ['small', 'large', 'default'])
      }
    },
    labelInValue: {
      type: Boolean,
      default: false
    },
    notFoundText: {
      type: String
    },
    placement: {
      validator(value) {
        return oneOf(value, [
          'top',
          'top-start',
          'top-end',
          'bottom',
          'bottom-start',
          'bottom-end',
          'left',
          'left-start',
          'left-end',
          'right',
          'right-start',
          'right-end'
        ])
      },
      default: 'bottom'
    },
    width: {
      type: [String, Number]
    },
    transfer: {
      type: Boolean,
      default: false
    },
    autoComplete: {
      type: Boolean,
      default: false
    },
    isString: {
      //多选时支持string类型，选项并以','隔开
      type: Boolean,
      default: false
    },
    isCheckall: {
      //多选 是否显示全选
      type: Boolean,
      default: false
    },
    noMoreText: {
      //下拉加载完成数据提示
      type: String
    },
    isComputed: {
      //是否显示加载完成数据提示
      type: Boolean,
      default: false
    },
    isArrow: {
      //typefield时有需求不显示箭头
      type: Boolean,
      default: true
    },
    algin: {
      type: String,
      default: 'left'
    },
    setDefSelect: {
      //如果选择为空
      type: Boolean,
      default: false
    },
    dropWidth: {
      //下拉框的宽度
      type: [String, Number]
    },
    searchHolder: {
      type: String
    },
    showBottom: {
      type: Boolean,
      default: false
    },
    specialIndex: {
      type: Boolean,
      default: false
    },
    checkToHead: {
      //只在多选且筛选框下有效
      type: Boolean,
      default: false
    },
    showTitle: {
      type: Boolean,
      default: false
    },
    hideMult: {
      type: Boolean,
      default: false
    },
    focusSelect: {
      type: Boolean,
      default: true
    },
    // o45证券代码--远程搜索时，点击输入框后不显示下拉列表，值改变时才显示，搜索值清空后隐藏
    remoteFocusNotShowList: {
      type: Boolean,
      default: false
    },
    // 滚动时先固定滚动条，只有滚动至当屏最上方或者最下方时进行滚动条滚动，否则滚动条不动，只改变focusIndex值
    scrollFix: {
      type: Boolean,
      default: false
    },
    isBackClear: {
      type: Boolean,
      default: false
    },
    autoPlacement: {
      type: Boolean,
      default: false
    },
    zeroToNull: {
      type: Boolean,
      default: false
    },
    // 宽度自适应
    widthAdaption: {
      type: Boolean,
      default: false
    },
    maxDropWidth: {
      type: [String, Number],
      default: 500
    },
    /* 条目不存在时允许增加新条目 */
    allowCreate: {
      type: Boolean,
      default: false
    },
    /* 多选时是否将选项压缩展示个数 */
    collapseTags: {
      type: Boolean,
      default: false
    },
    /* 搜索时是否不将焦点放在第一搜索项 */
    notAutoFocus: {
      type: Boolean,
      default: false
    },
    /* 全选和取消全选是否根据检索后展示选项 */
    isSelectFilter: {
      type: Boolean,
      default: false
    },
    showValue: {
      type: Boolean,
      default: false
    },
    tabindex: {
      type: [String, Number],
      default: 0,
      validator(value) {
        let num = parseInt(value)
        return num <= 32767 && num >= -1
      }
    },
    maxlength: {
      type: Number,
      default: Infinity
    },
    // o45全选按钮在顶部
    buttonToTop: {
      type: Boolean,
      default: false
    },
    // 改变初始化vlaue值的类型，考虑到初始化取值时，value和对应的option值类型不同（e.g: Number 1 和 Strign 1）
    // 该属性表示初始化value值是转化成String类型
    initValueTypeToString: {
      type: Boolean,
      default: false
    },
    // 当 multiple remote-method时，设置此属性 query在控件不失去焦点的情况下，query保留之前的搜索值
    // 当 dropdown隐藏时，控件失去焦点，query = ‘’， 触发remoteMethod()
    queryNotEmpty: {
      type: Boolean,
      default: false
    },
    // 场外select放在poptip中渲染，弹出框dropdown与poptip的z-index相同导致选项被遮挡
    selectInPoptip: {
      type: Boolean,
      default: false
    },
    customTransferClassName: {
      type: String
    },
    // v-model的值跟option下拉框的值不匹配（即脏数据），需要进行的操作判断
    // true 表示如果value值跟下拉选择不匹配（脏数据）会把v-model的清空，默认为false不清空,保留原值
    // 不支持远程搜索 不支持allowCreate
    valueNotMatch: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      prefixCls: prefixCls,
      visible: false,
      selectTabindex: this.filterable ? 0 : this.tabindex,
      options: [],
      disabledOpts: [],
      optionInstances: [],
      selectedSingle: '',
      selectedMultiple: [],
      focusIndex: 0,
      query: '',
      lastQuery: '',
      selectToChangeQuery: false,
      slotChangeDuration: false,
      model: null,
      // model: [],
      currentLabel: this.label,
      isInputFocus: false, //是否焦点，为false时触发blur校验
      isLi: true,
      scrollBarWidth: getScrollBarSize(),
      isfirstSelect: false,
      selectHead: false,
      titleTip: '',
      isSelectAll: false,
      typeValue: 'string',
      focusValue: '',
      viewValue: null,
      lastScrollTop: 0,
      inputLastQuery: '' //记录上一次input键盘主动输入值
    }
  },
  computed: {
    notFound() {
      return this.optionInstances.every(op => op.hidden)
    },
    showHeader() {
      return this.$slots.header || this.buttonToTop
    },
    classes() {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}-visible`]: this.visible,
          [`${prefixCls}-disabled`]: this.disabled,
          [`${prefixCls}-editable`]: !this.editable,
          [`${prefixCls}-readonly`]: this.readonly,
          [`${prefixCls}-multiple`]: this.multiple,
          [`${prefixCls}-single`]: !this.multiple,
          [`${prefixCls}-show-clear`]: this.showCloseIcon,
          [`${prefixCls}-${this.size}`]: !!this.size,
          [`${prefixCls}-hideMult`]: this.hideMult && this.multiple
        }
      ]
    },
    checkHeadClass() {
      return {
        [`${prefixCls}-checkHead`]:
          this.checkToHead && this.showBottom && this.filterable
      }
    },
    hideMultHead() {
      return {
        [`${prefixCls}-hideMultHead`]: this.hideMult && this.multiple,
        [`${prefixCls}-hideMultHead-select`]: this.isSelectAll
      }
    },
    dropdownCls() {
      return {
        [prefixCls + '-dropdown-transfer']:
          findComponentParent(this, ['Msgbox', 'Drawer']) && this.transfer, // Select 只有在 Msgbox,Drawer 中才需要增加这个 transfer 类,防止z-index 覆盖
        [prefixCls + '-multiple']: this.multiple && this.transfer,
        ['h-auto-complete']: this.autoComplete,
        [prefixCls + '-dropdown-inpoptip']: this.selectInPoptip,
        [`${this.customTransferClassName}`]: this.customTransferClassName
      }
    },
    content() {
      return `${prefixCls}-dropdown-content`
    },
    selectionCls() {
      return [
        `${prefixCls}-${this.algin}`,
        {
          [`${prefixCls}-selection`]: !this.autoComplete
        }
      ]
    },
    showPlaceholder() {
      let status = false
      if (typeof this.model === 'string') {
        if (this.model === '') {
          status = true
        }
      } else if (Array.isArray(this.model)) {
        if (!this.model.length) {
          status = true
        }
      } else if (this.model === null) {
        status = true
      }
      return status
    },
    showCloseIcon() {
      return (
        ((!this.multiple && this.clearable) ||
          (this.multiple && this.multClearable)) &&
        !this.showPlaceholder
      )
    },
    showRemoteIcon() {
      if (this.remote && this.remoteIcon != '') {
        return true
      } else {
        return false
      }
    },
    localePlaceholder() {
      if (this.placeholder === undefined) {
        return this.t('i.select.placeholder')
      } else {
        return this.placeholder
      }
    },
    localeSearchHolder() {
      if (this.searchHolder === undefined) {
        return this.t('i.select.searchHolder')
      } else {
        return this.searchHolder
      }
    },
    localeNotFoundText() {
      if (this.notFoundText === undefined) {
        return this.t('i.select.noMatch')
      } else {
        return this.notFoundText
      }
    },
    localeLoadingText() {
      if (this.loadingText === undefined) {
        return this.t('i.select.loading')
      } else {
        return this.loadingText
      }
    },
    localeNoMoreText() {
      if (this.noMoreText === undefined) {
        return this.t('i.select.noMoreData')
      } else {
        return this.noMoreText
      }
    },
    dropVisible() {
      let dropVisible = false
      let status = true
      // 去除当远程检索 query =’‘ 时， 下拉面板不收起
      // if (!this.loading && this.remote && this.query === '' && !options.length) status = false;
      if (this.remote && this.showBottom) status = true
      let bol = this.visible && status
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      if (!bol && this.queryNotEmpty) this.query = ''
      dropVisible = this.visible && status
      if (this.$refs.input) {
        // eslint-disable-next-line vue/no-async-in-computed-properties
        setTimeout(() => {
          dropVisible && this.$refs.input.select()
          !dropVisible && this.$refs.input.blur()
        })
      }
      return dropVisible
    },
    selectInputStyles() {
      return {
        width: `${this.width}px`
      }
    },
    multiplestyle() {
      return {
        width: `${this.width}px`
      }
    },
    // eslint-disable-next-line vue/return-in-computed-property
    inputVisibleStyle() {
      if (this.filterable && this.multiple) {
        let style = {}
        if (!this.isInputFocus && this.selectedMultiple.length !== 0) {
          style.height = '1px'
          style.lineHeight = '1px'
          style.position = 'absolute'
          style.bottom = 0
          style.opacity = 0
        } else {
          let el = this.$refs.reference
          if (el) {
            style.height = el.clientHeight + 'px'
            style.lineHeight = el.offsetHeight + 'px'
          }
        }
        return style
      }
    },
    checkAll() {
      // 当不显示全选 全不选时候，去除其padding
      if (
        !(
          this.isCheckall &&
          this.multiple &&
          !this.notFoundShow &&
          !this.buttonToTop
        ) ||
        (this.enableCreate && this.showNewOption)
      ) {
        return `${prefixCls}-checkall ${prefixCls}-check-no-padding`
      }
      return `${prefixCls}-checkall`
    },
    notFoundShow() {
      let options = this.options || this.$slots.default
      options = options || []
      let state =
        (this.notFound && !this.remote) ||
        (this.remote && !this.loading && !options.length) ||
        (!this.remote && !options.length)
      return state
    },
    showFooter() {
      return this.$slots.footer || this.isCheckall
    },
    /* 是否开启新建条目功能 */
    enableCreate() {
      return this.allowCreate && this.filterable
    },
    showNewOption() {
      const query = this.query
      const optionInstances = this.optionInstances
      let existed = false
      for (let i in optionInstances) {
        let option = optionInstances[i]
        if (
          option.showLabel === query ||
          option.value === query ||
          option.searchLabel == query
        ) {
          existed = true
          break
        }
      }
      return query && !existed
    }
  },
  methods: {
    multipleNumberCheck(model) {
      let number = Number(this.multipleNumber) || 0
      if (typeof model === 'string') model = model.split(',')
      if (
        this.multiple &&
        this.multipleNumber !== null &&
        model.length > number
      ) {
        Message.error(`最多可以选择${number}个`)
        return true
      }
      return false
    },
    handleInputKeyup(event) {
      this.inputLastQuery = this.query
      this.$emit('on-keyup', this.query, event)
    },
    handleInputKeydown(event) {
      this.$emit('on-keydown', this.query, event)
    },
    handleclick(e) {
      e.stopPropagation()
    },
    keyup(event) {
      if (event.keyCode == 9) {
        if (this.disabled || this.readonly || !this.editable) {
          return false
        } else {
          this.toggleMenu()
        }
      }
    },
    keydown(event) {
      // if (event.keyCode == 9) {
      //   this.hideMenu();
      // }
      this.handleKeydown(event)
    },
    focus() {
      this.isInputFocus = true
      if (this.disabled || this.readonly) return
      this.$nextTick(() => {
        this.visible = true
        if (this.filterable) {
          this.$refs.input.focus()
        } else {
          this.$refs.reference.focus()
        }
        this.$emit('on-focus')
      })
    },
    blur() {
      if (this.multiple) {
        // 多选返回数组
        this.dispatch('FormItem', 'on-form-blur', this.selectedMultiple)
      } else {
        // 单选返回字符串
        this.dispatch('FormItem', 'on-form-blur', this.selectedSingle)
      }
      this.isInputFocus = false
      this.visible = false
      if (this.filterable) {
        this.$refs.input.blur()
      } else {
        this.$refs.reference.blur()
      }
    },
    select() {
      if (this.filterable) {
        this.$refs.input.focus()
        this.$refs.input.select()
      }
    },
    handleSelectScroll(event) {
      let num = getBarBottom(event.target, this.scrollBarWidth)
      this.$emit(
        'on-scroll',
        num,
        this.lastScrollTop !== event.target.scrollTop ? 'y' : 'x'
      )
      this.lastScrollTop = event.target.scrollTop
    },
    toggleCheck() {
      let arr = []
      this.optionInstances.forEach(item => {
        if (this.isSelectFilter) {
          if (!item.hidden) {
            if (findInx(this.model, val => val == item.value) === -1) {
              arr.push(item.value)
            }
          }
        } else {
          arr.push(item.value)
        }
      })
      return this.multipleNumberCheck(arr)
    },
    toggleSelect(val, isNotSelectAll = false) {
      if (!isNotSelectAll && this.toggleCheck()) {
        this.$nextTick(() => {
          this.selectHead = false
        })
        return
      }
      this.isSelectAll = !this.isSelectAll
      const createdOption = this.$refs.createdOption
      const isSelectFilter = this.isSelectFilter
      if (val) {
        if (this.specialIndex) {
          this.model = this.typeValue == 'string' ? ['-1'] : [-1]
        } else {
          let arr = []
          this.optionInstances.forEach(item => {
            if (isSelectFilter) {
              if (!item.hidden) {
                if (findInx(this.model, val => val == item.value) === -1) {
                  this.model.push(item.value)
                }
              }
            } else {
              arr.push(item.value)
            }
          })
          if (!isSelectFilter) this.model = arr
          if (createdOption) {
            createdOption.select()
            createdOption.selected = true
          }
        }
      } else {
        if (createdOption) {
          createdOption.selected = false
          let index = findInx(this.model, val => val == createdOption.value)
          if (index > -1) {
            this.model.splice(index, 1)
          }
        }
        if (isSelectFilter) {
          this.selectHead = false
          this.optionInstances.forEach(item => {
            if (!item.hidden) {
              let index = findInx(this.model, val => val == item.value)
              if (index > -1) {
                this.model.splice(index, 1)
              }
            }
          })
        } else {
          this.model = []
          this.selectHead = false
        }
      }
    },
    offsetArrow() {
      if (!this.multiple) return
      let el = this.$refs.reference
      if (el.scrollHeight > el.clientHeight) {
        if (this.$refs.arrowb) this.$refs.arrowb.$el.style.right = '22px'
        if (this.$refs.close) this.$refs.close.$el.style.right = '39px'
      } else {
        if (this.$refs.arrowb) this.$refs.arrowb.$el.style.right = '8px'
        if (this.$refs.close) this.$refs.close.$el.style.right = '25px'
      }
    },
    toggleMenu() {
      if (this.disabled || this.readonly || !this.editable) {
        return false
      }
      // o45证券代码--点击时,若输入框值==当前选中value,则需要隐藏下拉列表
      if (
        this.model == this.query &&
        this.model == this.value &&
        typeof this.value == 'string' &&
        this.remoteFocusNotShowList &&
        !this.multiple
      ) {
        this.visible = false
        // focusSelect为false时，表明输入框获取焦点后直接进行搜索
        if (!this.focusSelect) this.selectToChangeQuery = false
      } else {
        this.visible = !this.visible
      }
      this.isInputFocus = true
    },
    fold() {
      this.visible = false
    },
    hideMenu() {
      // 原有hanldeBlur里面单选失焦是否清空逻辑移动到这里判断【单选远程搜索，无需在失焦时移除，而是在下拉框真正关闭时移除】
      if (this.model == '') {
        this.query = ''
      }
      this.visible = false
      if (!window.isO45) {
        this.focusIndex = 0
      }
      this.broadcast('Option', 'on-select-close')
    },
    findChild(cb) {
      const find = function(child) {
        const name = child.$options.componentName
        if (name) {
          if (child.created) return
          cb(child)
        } else if (child.$children.length) {
          child.$children.forEach(innerChild => {
            find(innerChild, cb)
          })
        }
      }
      if (this.optionInstances.length) {
        this.optionInstances.forEach(child => {
          find(child)
        })
      } else {
        this.$children.forEach(child => {
          find(child)
        })
      }
    },
    updateOptions(init, slot = false) {
      let options = []
      let disabledOpts = []
      let index =
        this.enableCreate && typeof this.$refs.createdOption !== 'undefined'
          ? 2
          : 1
      this.optionInstances = []
      this.findChild(child => {
        options.push({
          value: child.value,
          // label: (child.label === undefined) ? child.$el.innerHTML.slice(Number(child.$el.innerHTML.indexOf('</label>')+8)) : child.label
          // label: (child.label === undefined) ? child.$el.innerText.replace(/\s*\w{4,5} /, '') : child.label
          //修复 原生IE11浏览器下 回显成 false+label的文案
          // label: (child.label === undefined) ? child.$el.innerHTML.split('</label> ')[1] : child.label
          // #193343 兼容select下拉选项自定义时候，用户没有在option传递lable属性时（只有在用户自定义内容比较单一）
          // eg: <span style="color: red;">lebel 1</span>
          label: child.label === undefined ? child.searchLabel : child.label
        })
        if (child.disabled) {
          disabledOpts.push(child.value)
        }
        child.index = index++
        if (init) {
          this.optionInstances.push(child)
        }
      })
      this.options = options
      this.disabledOpts = disabledOpts
      if (this.specialIndex && this.options.length > 0) {
        this.typeValue = typeOf(this.options[0].value)
      }
      if (init) {
        if (!this.remote) {
          this.updateSingleSelected(true, slot)
          this.updateMultipleSelected(true, slot)
        }
      }
    },
    updateSingleSelected(init = false, slot = false) {
      const type = typeof this.model
      if (type === 'string' || type === 'number') {
        let findModel = false
        let curSingle = ''
        for (let i = 0; i < this.options.length; i++) {
          if (this.model === this.options[i].value) {
            curSingle = this.options[i].label
            findModel = true
            break
          }
        }
        this.selectedSingle = curSingle
        if (this.enableCreate && !findModel) {
          this.selectedSingle = this.model
          findModel = true
        }
        if (!findModel && this.model !== '' && !this.remote) {
          // this.$emit('on-not-find-model')  // 事件为当v-model的值跟option的value值不匹配的时触发该事件
           // #205377 单选下，Select 在v-model不匹配option值的时候，v-model清空，表格单元项必填时，校验必填
           // 不支持远程
          this.valueNotMatch  && ( this.model = '' )
        }
         // 在初始化数据选中的时候，query清空
        if (slot && !findModel) {
          this.model = ''
          this.query = ''
        }
      }
      // 解决 on-change时间 造成用户获取viewvalue延迟
      this.viewValue = this.selectedSingle
      this.toggleSingleSelected(this.model, init)
    },
    handleIconClose() {
      if (this.readobly || this.disabled || !this.editable) return false
      if (this.multiple) {
        this.clearMultipleSelect()
      } else {
        this.clearSingleSelect()
      }
      this.isInputFocus = true
    },
    clearMultipleSelect() {
      let disArr = []
      this.model.forEach(col => {
        if (this.disabledOpts.indexOf(col) != -1) {
          disArr.push(col)
        }
      })
      this.model = disArr
    },
    clearSingleSelect() {
      if (this.showCloseIcon) {
        this.findChild((child) => {
          child.selected = false
        })
        this.model = ''
        if (this.filterable && !this.showBottom) {
          this.query = ''
        }
        this.hideMenu()
        // 需求200245 当showTitle为true时鼠标移上来时如果无值支持显示placeholder
        if (this.showTitle) {
          this.titleTip = this.localePlaceholder
        } else {
          this.titleTip = ''
        }
      }
    },
    updateMultipleSelected(init = false, slot = false) {
      if (this.multiple && Array.isArray(this.model)) {
        // let selected = this.remote && this.model.length > 0 ? this.selectedMultiple : [];
        let selectedMultiple = this.selectedMultiple
        let selected = []
        for (let i = 0; i < this.model.length; i++) {
          const model = this.model[i]
          let option
          for (let op of this.options) {
            if (op.value == model) {
              option = op
              break
            }
          }
          // 处理远程搜索已选中选项不在当前展示的选项列表中的情况
          if (!option && this.remote) {
            for (let op of selectedMultiple) {
              if (op.value === model) {
                option = op
                break
              }
            }
          }
          if (option) {
            selected.push({
              value: option.value,
              label: option.label
            })
          } else if (this.enableCreate) {
            selected.push({
              value: model,
              label: model
            })
          }
        }
        const selectedArray = []
        const selectedObject = {}
        selected.forEach(item => {
          if (!selectedObject[item.value]) {
            selectedArray.push(item)
            selectedObject[item.value] = 1
          }
        })
        this.selectedMultiple = this.remote ? selectedArray : selected
        if (slot) {
          let selectedModel = []
          for (let i = 0; i < selected.length; i++) {
            selectedModel.push(selected[i].value)
          }
          if (this.model.length === selectedModel.length) {
            this.slotChangeDuration = true
          }
          this.model = selectedModel
        }
      }
      // 解决 on-change时间 造成用户获取viewvalue延迟
      this.viewValue = this.selectedMultiple
      this.toggleMultipleSelected(
        init && this.multiple && this.model === '' ? [] : this.model,
        init
      )
    },
    removeTag(index, item, type) {
      // type = delete 表示输入backspace
      let removeItem = this.selectedMultiple[index]
      if (this.disabled || this.readonly || !this.editable) {
        return false
      }
      if (item) {
        // 修正 index 与 item 不匹配的问题
        index = this.model.findIndex(value => value === item.value)
      }
      if (this.remote || this.enableCreate) {
        const tag = this.model[index]
        this.selectedMultiple = this.selectedMultiple.filter(
          item => item.value !== tag
        )
      }
      if (this.disabledOpts.indexOf(this.model[index]) == -1) {
        this.model.splice(index, 1)
      } else {
        return
      }
      if (this.filterable && this.visible && !this.showBottom) {
        this.$refs.input.focus()
      }
      this.isInputFocus = true
      // #193183 当多选点击×符号时,或者filterable&&this.isBackClear时候，新增且触发on-remove事件
      // 1. 点击 × 2. filterable && !isBackClear backspace 清除 3.直接点击下拉列表已选中的项
      // 当键盘输入backspace，设置isBackClear：true，导致选中项全部清空，不触发on-item-remove
      // if (type === 'delete' && this.isBackClear ) return
      // this.$emit('on-item-remove',removeItem, index)
      if (!type) {
        this.$emit('on-item-remove', removeItem, index)
      } else if (type === 'delete') {
        !this.isBackClear && this.$emit('on-item-remove', removeItem, index)
      }
    },
    toggleSingleSelected(value, init = false) {
      if (!this.multiple) {
        let label = ''
        const createdOption = this.$refs.createdOption
        if (createdOption) {
          if (value.indexOf(createdOption.value) > -1) {
            createdOption.selected = true
          }
        }
        this.findChild(child => {
          if (child.value === value) {
            child.selected = true
            // label: (child.label === undefined) ? child.$el.innerHTML.slice(Number(child.$el.innerHTML.indexOf('</label>')+8)) : child.label
            // label = (child.label === undefined) ? child.$el.innerText.replace(/\s*\w{4,5} /, '') : child.label;
            // label = (child.label === undefined) ? child.$el.innerHTML.split('</label> ')[1] : child.label
            label = child.label === undefined ? child.searchLabel : child.label
          } else {
            child.selected = false
          }
        })
        // this.hideMenu();
        if (!init) {
          if (this.labelInValue) {
            this.$emit('on-change', {
              value: value,
              label: label ? label : value
            })
            this.dispatch('FormItem', 'on-form-change', {
              value: value,
              label: label ? label : value
            })
          } else {
            this.$emit('on-change', value)
            this.dispatch('FormItem', 'on-form-change', value)
          }
        }
      }
    },
    toggleMultipleSelected(value, init = false) {
      if (this.multiple) {
        let curSelect = true
        let hybridValue = []
        for (let i = 0; i < value.length; i++) {
          hybridValue.push({
            value: value[i]
          })
        }
        this.findChild(child => {
          const index = findInx(value, val => val == child.value)
          if (index >= 0) {
            child.selected = true
            hybridValue[index].label =
              child.label === undefined ? child.searchLabel : child.label
            // hybridValue[index].label  = (child.label === undefined) ? child.$el.innerHTML.split('</label> ')[1] : child.label
            // hybridValue[index].label  = (child.label === undefined) ? child.$el.innerText.replace(/\s*\w{4,5} /, '') : child.label;
            // hybridValue[index].label = (child.label === undefined) ? child.$el.innerHTML.slice(Number(child.$el.innerHTML.indexOf('</label>')+8)) : child.label;
          } else {
            child.selected = false
          }
          if (curSelect && child.selected == false) {
            curSelect = false
          }
        })
        // 新建条目的label等于value
        if (this.enableCreate) {
          hybridValue.forEach(v => {
            if (typeof v.label === 'undefined') {
              v.label = v.value
            }
          })
          const createdOption = this.$refs.createdOption
          if (createdOption) {
            createdOption.selected = value.indexOf(createdOption.value) >= 0
            if (curSelect && !createdOption.selected) {
              curSelect = false
            }
          }
        }
        this.isSelectAll = curSelect
        if (!init) {
          if (this.labelInValue) {
            this.$emit('on-change', hybridValue)
            this.dispatch('FormItem', 'on-form-change', hybridValue)
          } else {
            if (this.multipleNumber && this.multipleNumberCheck(this.model))
              return
            this.$emit('on-change', value)
            this.dispatch('FormItem', 'on-form-change', value)
          }
        }
      }
    },
    handleClose() {
      this.hideMenu()
      if (this.isInputFocus) {
        if (this.multiple) {
          // 多选返回数组
          this.dispatch('FormItem', 'on-form-blur', this.selectedMultiple)
        } else {
          // 单选返回字符串
          this.dispatch('FormItem', 'on-form-blur', this.selectedSingle)
        }
        this.isInputFocus = false
      }
    },
    handleKeydown(e) {
      this.isKeyDown = true // switch on key down status
      this.keyboardEvent = e // cache current keyboard event
      if (this.visible) {
        const keyCode = e.keyCode
        // Esc slide-up
        if (keyCode === 27) {
          e.preventDefault()
          this.hideMenu()
        }
        if (window.isO45) {
          // right
          if (keyCode === 39) {
            e.preventDefault()
            this.navigateOptions('next')
          }
          // left
          if (keyCode === 37) {
            e.preventDefault()
            this.navigateOptions('prev')
          }
          if ((keyCode === 39 || keyCode === 37) && !this.multiple) {
            this.model = this.focusValue
            this.selectToChangeQuery = true
          }
          if (this.multiple && keyCode === 32) {
            //空格键
            e.preventDefault()
            const createdOption = this.$refs.createdOption
            if (createdOption && createdOption.isFocus) {
              createdOption.select()
              // 单选时，enter不需要取消选择
              if (this.multiple || !createdOption.selected) {
                createdOption.selected = !createdOption.selected
              }
            } else {
              this.findChild(child => {
                if (child.isFocus) {
                  child.select()
                }
              })
            }
          }
          return false
        }
        // next
        if (keyCode === 40) {
          e.preventDefault()
          this.navigateOptions('next')
        }
        // prev
        if (keyCode === 38) {
          e.preventDefault()
          this.navigateOptions('prev')
        }
        // enter
        if (keyCode === 13 || (keyCode === 9 && this.tabToSave)) {
          keyCode === 13 && e.preventDefault()
          const createdOption = this.$refs.createdOption
          if (createdOption && createdOption.isFocus) {
            createdOption.select()
            // 单选时，enter不需要取消选择
            if (this.multiple || !createdOption.selected) {
              createdOption.selected = !createdOption.selected
            }
          } else {
            if (!this.multiple && window.IS_LICAI) {
              this.model = this.focusValue
              this.selectToChangeQuery = true
            } else {
              this.findChild(child => {
                if (child.isFocus) {
                  child.select()
                }
              })
            }
          }
        }
      }
      // 当键盘按下tab键 选中下一个元素，隐藏当前下拉drop元素
      if (e.keyCode == 9) {
        let dom =
          (findComponentParent(this, 'FormItem') &&
            findComponentParent(this, 'FormItem').onlyBlurRequire) ||
          (findComponentParent(this, 'Form') &&
            findComponentParent(this, 'Form').onlyBlurRequire)
        if (this.multiple && dom)
          this.dispatch('FormItem', 'on-form-blur', this.selectedMultiple)
        this.hideMenu()
      }
      if (this.visible || this.isInputFocus) {
        this.handleBack(e)
      }
    },
    navigateOptions(direction) {
      let curTop = this.$refs.content.scrollTop
        ? this.$refs.content.scrollTop
        : 0
      const createdOption = this.$refs.createdOption
      let maxIndex = createdOption
        ? this.options.length + 1
        : this.options.length
      if (this.options.length === 0) return
      if (direction === 'next') {
        const next = this.focusIndex + 1
        this.focusIndex = this.focusIndex === maxIndex ? 1 : next
      } else if (direction === 'prev') {
        const prev = this.focusIndex - 1
        this.focusIndex = this.focusIndex <= 1 ? maxIndex : prev
      }
      let child_status = {
        disabled: false,
        hidden: false
      }
      let find_deep = false
      if (createdOption && this.focusIndex === 1) {
        createdOption.isFocus = true
        this.findChild(child => {
          child.isFocus = false
        })
      } else {
        if (createdOption) {
          createdOption.isFocus = false
        }
        this.findChild(child => {
          if (child.index === this.focusIndex) {
            child_status.disabled = child.disabled
            child_status.hidden = child.hidden
            if (!child.disabled && !child.hidden) {
              child.isFocus = true
            }
          } else {
            child.isFocus = false
          }
          if (!child.hidden && !child.disabled) {
            find_deep = true
          }
        })
      }

      this.focusValue = this.options[this.focusIndex - 1].value
      let top = 30 * (this.focusIndex - 1)
      let contentHeight = 0
      let selectItemHeight = 1
      if (this.scrollFix) {
        // 距离底部5px
        selectItemHeight =
          ((!this.notFound && !this.remote) ||
            (this.remote && !this.loading && !this.notFound)) &&
          this.$refs.content.getElementsByClassName('h-select-item').length > 0
            ? this.$refs.content.getElementsByClassName('h-select-item')[0]
              .clientHeight
            : 30
        contentHeight = this.showBottom
          ? this.$refs.content.clientHeight - 10 - selectItemHeight
          : this.$refs.content.clientHeight - 10
        if (direction == 'next') {
          let conNum = Math.floor(contentHeight / selectItemHeight)
          let topNum = Math.floor(curTop / selectItemHeight)
          top =
            this.focusIndex == 1
              ? 0
              : this.focusIndex < conNum + topNum
                ? curTop
                : (this.focusIndex - conNum) * selectItemHeight
        } else if (direction === 'prev') {
          let maxnum = Math.floor((contentHeight + curTop) / selectItemHeight)
          let minnum = Math.floor(curTop / selectItemHeight)
          top =
            this.focusIndex > minnum && this.focusIndex < maxnum
              ? curTop
              : (this.focusIndex - 1) * selectItemHeight
        }
      }
      if (curTop != top) {
        this.$refs.content.scrollTop = top
      }
      this.resetScrollTop()
      if ((child_status.disabled || child_status.hidden) && find_deep) {
        this.navigateOptions(direction)
      }
    },
    resetScrollTop() {
      const index = this.focusIndex - 1
      let bottomOverflowDistance = this.optionInstances[index]
        ? this.optionInstances[index].$el.getBoundingClientRect().bottom -
          this.$refs.dropdown.$el.getBoundingClientRect().bottom
        : 0
      let topOverflowDistance = this.optionInstances[index]
        ? this.optionInstances[index].$el.getBoundingClientRect().top -
          this.$refs.dropdown.$el.getBoundingClientRect().top
        : 0
      if (bottomOverflowDistance > 0) {
        this.$refs.dropdown.$el.scrollTop += bottomOverflowDistance
      }
      if (topOverflowDistance < 0) {
        this.$refs.dropdown.$el.scrollTop += topOverflowDistance
      }
    },
    handleFocus() {
      this.isInputFocus = true
      this.$emit('on-focus')
    },
    handleBlur() {
      if (this.multiple && this.filterable) {
        setTimeout(() => {
          this.$refs.reference.scrollTop = 0
        }, 200)
      }

      this.isInputFocus = false
      this.$emit('on-blur')
      if (this.showBottom) return false
      // 场外提出筛选情况下，鼠标点选按住不松开会导致输入框内容被清空下拉项刷新无法选择想要的项（沟通后暂时先不去掉延时代码）
      setTimeout(() => {
        const model = this.model
        if (this.multiple) {
          if (!this.queryNotEmpty) this.query = ''
        } else {
          if (model !== '') {
            let found = false
            this.findChild(child => {
              if (child.value === model) {
                found = true
                this.query =
                  child.label === undefined ? child.searchLabel : child.label
                this.query = this.query.trim()
              }
            })
            // 如果删除了搜索词，下拉列表也清空了，所以强制调用一次remoteMethod
            if (
              (this.remote || this.enableCreate) &&
              !found &&
              this.query !== this.lastQuery
            ) {
              this.$nextTick(() => {
                this.query = this.lastQuery.trim()
              })
            }
          } else {
            // this.query = ''
          }
        }
        if (this.multiple) {
          // 多选返回数组
          let dom =
            (findComponentParent(this, 'FormItem') &&
              findComponentParent(this, 'FormItem').onlyBlurRequire) ||
            (findComponentParent(this, 'Form') &&
              findComponentParent(this, 'Form').onlyBlurRequire)
          !dom &&
            this.dispatch('FormItem', 'on-form-blur', this.selectedMultiple)
        } else {
          // 单选返回字符串
          this.dispatch('FormItem', 'on-form-blur', this.selectedSingle)
        }
      }, 300)
    },
    resetInputState() {
      this.inputLength = this.$refs.input.value.length * 12 + 56
    },
    handleInputDelete() {
      if (this.multiple && this.model.length && this.query === '') {
        this.removeTag(this.model.length - 1, undefined, 'delete')
      }
    },
    // use when slot changed
    slotChange() {
      this.options = []
      this.optionInstances = []
    },
    setQuery(query) {
      if (!this.filterable) return
      this.inputLastQuery = query
      this.query = query
    },
